import { useEffect, useRef } from "react";

function useInterval(callback: () => void, delay: number | null) {
    const savedCallback = useRef<() => void>(); //   callback  ref  .

    useEffect(() => {
        savedCallback.current = callback; // callback   ref  .
    }, [callback]);

    useEffect(() => {
        function tick() {
            if (savedCallback.current) savedCallback.current(); // tick  callback  .
        }
        if (delay !== null) {
            //  delay null 
            const id = setInterval(tick, delay); // delay  interval  .
            return () => clearInterval(id); // unmount  clearInterval .
        }
    }, [delay]); // delay    .
}

export default useInterval;
